"use client";

import { Container } from "@/components/common/container";
import React, { useEffect, useState } from "react";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { EllipsisVertical, Link2 } from "lucide-react";
import { Card } from "@/components/ui/card";

export default function AllTypeCategory() {
    const [categories, setCategories] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const fetchCategories = async () => {
            try {
                const token = localStorage.getItem("token");

                if (!token) {
                    throw new Error(
                        "Access Denied: No token provided. Please login first."
                    );
                }

                const response = await fetch(
                    "https://wiqiapi.testenvapp.com/api/admin/getCategory",
                    {
                        method: "GET",
                        headers: {
                            "Content-Type": "application/json",
                            Authorization: `Bearer ${token}`,
                        },
                    }
                );

                const data = await response.json();

                if (!response.ok || !data.success) {
                    throw new Error(
                        data.message || "Something went wrong while fetching categories"
                    );
                }

                if (data?.data?.data && Array.isArray(data.data.data)) {
                    setCategories(data.data.data);
                } else {
                    throw new Error("Invalid category data format");
                }
            } catch (err) {
                console.error("Error fetching categories:", err);
                setError(err.message || "Failed to fetch categories");
            } finally {
                setLoading(false);
            }
        };

        fetchCategories();
    }, []);

    if (loading) {
        return (
            <div className="flex justify-center items-center min-h-[200px]">
                <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
                <span className="ml-3">Loading categories...</span>
            </div>
        );
    }

    if (error) {
        return (
            <div className="my-4 p-4 bg-red-100 text-red-700 rounded">
                <p className="font-bold">Error loading categories:</p>
                <p>{error}</p>
                <button
                    onClick={() => window.location.reload()}
                    className="mt-2 px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
                >
                    Retry
                </button>
            </div>
        );
    }

    return (
        <div className="py-8">
            <h1 className="text-2xl font-bold mb-8">Categories</h1>

            {categories.length === 0 ? (
                <div className="text-center py-12">
                    <p className="text-lg font-medium text-gray-600">ðŸš§ Coming Soon</p>
                </div>
            ) : (
                <Card className="overflow-x-auto">
                    <table className="w-full border-collapse">
                        <thead>
                            <tr className="bg-gray-100 text-left">
                                <th className="p-3 border">Image</th>
                                <th className="p-3 border">Name</th>
                                <th className="p-3 border">Title</th>
                                <th className="p-3 border">Type</th>
                                <th className="p-3 border">Action</th>
                            </tr>
                        </thead>
                        <tbody>
                            {categories.map((category) => (
                                <tr key={category._id} className="hover:bg-gray-50">
                                    <td className="p-3 border">
                                        <img
                                            src={category.image}
                                            alt={category.name}
                                            className="w-20 h-14 object-cover rounded"
                                            onError={(e) => {
                                                e.target.src =
                                                    "https://via.placeholder.com/150?text=No+Image";
                                            }}
                                        />
                                    </td>
                                    <td className="p-3 border font-bold">{category.name}</td>
                                    <td className="p-3 border">{category.title}</td>
                                    <td className="p-3 border capitalize">{category.type}</td>
                                    <td className="p-3 border">
                                        <div className="flex gap-2 items-center">
                                            {category.url && (
                                                <Link
                                                    href={category.url}
                                                    target="_blank"
                                                    className="px-3 py-1 bg-blue-100 text-blue-800 rounded-full text-sm flex gap-1 items-center hover:bg-blue-200"
                                                >
                                                    <Link2 size={16} /> Visit
                                                </Link>
                                            )}
                                            <div className="group relative">
                                                <EllipsisVertical className="cursor-pointer text-blue-800" />
                                                <div className="rounded-md border hidden group-hover:flex flex-col absolute top-6 bg-white end-0 z-10">
                                                    <p className="hover:bg-red-200 px-3 py-1 duration-300 cursor-pointer">
                                                        Edit
                                                    </p>
                                                    <p className="hover:bg-red-200 px-3 py-1 duration-300 cursor-pointer">
                                                        Delete
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </Card>
            )}
        </div>
    );
}
